import { trigger, transition, style, animate } from '@angular/animations';
import { ViewportScroller } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { fadeInAnimation, flashAnimation } from 'angular-animations';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
  animations: [
    trigger('showing', [
      transition('void => *', [
        style({opacity: 0}),
        animate(3000, style({opacity: 1}))
      ])
      ]),
      fadeInAnimation({duration:2000, anchor:'flash', delay: 1000}),
      flashAnimation({anchor: 'flash', duration: 4000, delay: 0}),
      
    ],
})
export class HomeComponent implements OnInit {
  anime: boolean = false;

  constructor(private viewportScroller: ViewportScroller) { }

  ngOnInit(): void {
  }

  goToNextSection(){
    const nextSection = 'what-we-do'
    this.viewportScroller.scrollToAnchor(nextSection);
  }
}
