import { Component, OnInit, ViewEncapsulation } from "@angular/core";

import { CompanyModel, LinkListModel, MarkerModel } from "../../models/";

@Component({
  selector: "app-contacts",
  templateUrl: "./contacts.component.html",
  styleUrls: ["./contacts.component.scss"],
  encapsulation: ViewEncapsulation.None,
})
export class ContactsComponent implements OnInit {
  private lat = 38.66541470884887;
  private lng = -27.248640015131546;

  company: CompanyModel;
  linkList: LinkListModel[];
  mailInfo: string;
  copyright: string;
  mapOptions: google.maps.MapOptions;
  marker: MarkerModel;

  constructor() { }

  ngOnInit(): void {

    this.company = {
      companyName: "AÇORBIZ",
      companyNameTag: "Representações Unipessoal Lda.",
      address: "Rua Félix Machado Barcelos, Nº 18",
      postalCode: "9700-202",
      city: "Angra do Heroísmo",
      phoneNumber: 924098356,
      email: "geral@acorbiz.pt",
    };

    this.linkList = [
      {
        url: "https://www.facebook.com/acorbizrepresentacoes",
        icon: "fa-facebook-square"
      }
    ];

    this.mapOptions = {
      center: {
        lat: this.lat,
        lng: this.lng,
      },
      zoom: 17,
      disableDefaultUI: false,
      zoomControl: true,
      mapTypeControl: true,
      streetViewControl: true,
      fullscreenControl: true,
    };

    this.marker = {
      position: {
        lat: this.lat,
        lng: this.lng,
      },
    };

    this.mailInfo = new Date().toLocaleString();
    this.copyright = this.getCopyright();
  }

  getCopyright(): string {
    let date: number =
      new Date().getFullYear() > 2010 && new Date().getFullYear();
    return "© " + date.toString() + " AÇORBIZ Representações Unipessoal Lda.";
  }
}
