import {
  Component,
  HostListener,
  OnInit,
} from '@angular/core';
import { element } from 'protractor';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent implements OnInit {
  private offset = 53;

  navbarLinkList = [
    {
      pageName: 'Serviços',
      pageLink: '#what-we-do',
      id: 'servicesLinkId',
    },
    {
      pageName: 'Sobre',
      pageLink: '#about-us',
      id: 'aboutLinkId',
    },
    {
      pageName: 'Contactos',
      pageLink: '#contacts',
      id: 'contactsLinkId',
    }
  ];

  constructor() { }

  ngOnInit(): void { }

  goTo(section: string) {
    let sectionElement: HTMLElement = document.querySelector(section);
    window.scrollTo({ top: sectionElement.offsetTop - this.offset });
  }

  @HostListener('window:scroll', [])
  onWindowScroll(): void {
    let activeSectionY = Math.abs(document.documentElement.getBoundingClientRect().y) + this.offset;

    let servicesSection: HTMLElement = document.querySelector('#what-we-do');
    let aboutSection: HTMLElement = document.querySelector('#about-us');
    let contactsSection: HTMLElement = document.querySelector('#contacts');

    if (activeSectionY >= servicesSection.offsetTop && activeSectionY < aboutSection.offsetTop) {
      document.querySelector('#servicesLinkId').classList.add('active');
    } else {
      document.querySelector('#servicesLinkId').classList.remove('active');
    }

    if (activeSectionY >= aboutSection.offsetTop && activeSectionY < contactsSection.offsetTop) {
      document.querySelector('#aboutLinkId').classList.add('active');
    } else {
      document.querySelector('#aboutLinkId').classList.remove('active');
    }

    if (activeSectionY >= contactsSection.offsetTop) {
      document.querySelector('#contactsLinkId').classList.add('active');
    } else {
      document.querySelector('#contactsLinkId').classList.remove('active');
    }
  }
}
